import slide1 from "../../images/slide1.png";
import slide2 from "../../images/slide2.png";
import slide3 from "../../images/slide3.png";

export const Carousel = () => {
  const slides = [
    { src: slide1, label: "Herramientas eléctricas" },
    { src: slide2, label: "Tornillería y fijaciones" },
    { src: slide3, label: "Pinturas y acabados" },
  ];

  return (
    <section
      style={{
        position: "relative",
        width: "100%",
        height: "300px",
        overflow: "hidden",
      }}
    >
      {slides.map((slide, i) => (
        <div
          key={i}
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            backgroundImage: `url(${slide.src})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            transition: "opacity 0.5s ease-in-out",
            opacity: i === 0 ? 1 : 0, // hardcode showing first slide
          }}
        >
          <div
            style={{
              position: "absolute",
              bottom: "16px",
              left: "16px",
              backgroundColor: "rgba(0,0,0,0.6)",
              color: "#ffffff",
              padding: "8px 12px",
              borderRadius: "4px",
              fontSize: "16px",
              fontWeight: "500",
            }}
          >
            {slide.label}
          </div>
        </div>
      ))}

      {/* Controles */}
      {["left", "right"].map((dir) => (
        <button
          key={dir}
          style={{
            position: "absolute",
            top: "50%",
            [dir]: "16px",
            transform: "translateY(-50%)",
            backgroundColor: "rgba(255,255,255,0.8)",
            border: "none",
            borderRadius: "50%",
            width: "32px",
            height: "32px",
            fontSize: "20px",
            cursor: "pointer",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          {dir === "left" ? "‹" : "›"}
        </button>
      ))}
    </section>
  );
};
